@import url(https://fonts.googleapis.com/css2?family=Pacifico&family=Raleway:wght@700&family=Roboto:wght@300;700&display=swap);


body {
    background: #222;
    font-family: Raleway;
    margin: 0;
    padding: 0;
}
.login-page, .signup-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}
.login-wrapper, .signup-wrapper {
  display: flex;
  grid-gap: 30px;
  gap: 30px;
  transform: rotate(45deg) rotate3d(1, -1, 0, 45deg);
}

.login, .signup {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
}

.login__header, .signup__header {
  padding-right: 55px;
  padding-bottom: 20px;
}

.login__login-form, .signup__signup-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.login-form__inputs, .signup-form__inputs {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.login-form__input, .signup-form__input {
  display: flex;
  align-items: center;
  width: 45vw; 
  height: 10vh; 
  max-width: 550px;
  min-width: 300px;
  min-height: 50px;
  max-height: 70px;
  background-color: white;
  margin: 10px;
  box-shadow: inset 1px 1px 5px 0px #ccc;
}

.login-form__input > i, .signup-form__input > i {
  margin: 15px;
  font-size: 30px;
}

.login-form__input > input, .signup-form__input > input {
  width: 35vw;
  max-width: 450px;
  min-width: 180px;
  height: 8vh; 
  max-height: 50px;
  min-height: 30px;
  font-family: Raleway;
  font-size: 30px;
  align-self: center;
  outline: none;
  border: none;

}

.login-form__button, .signup-form__button {
  display: flex;
  align-items: center;
  background-color: white;
  width: 45vw; 
  height: 10vh; 
  max-width: 550px;
  min-width: 300px;
  min-height: 50px;
  max-height: 70px;
  border: none;
  cursor: pointer;
  margin: 10px;
}


.login-form__button > i, .signup-form__button > i {
  position: absolute;
  font-size: 30px;
  margin: 10px; 
  pointer-events: none;
}

.login-form__button > input, .signup-form__button > input {
  background-color: #2EC980;
  font-family: Raleway;
  font-size: 25px;
  width: 45vw; 
  height: 10vh; 
  max-width: 550px;
  min-width: 300px;
  min-height: 50px;
  max-height: 70px; 
  border: none;
  box-shadow: inset 1px 1px 5px 0px rgba(29, 29, 29, 0.303);
  cursor: pointer;

}

.login-form__button > input:hover, .signup-form__button > input:hover {
  background-color: #89C9AB;
}

.login__text, .signup__text {
  font-family: raleway;
  font-size: 20px;
  color: #2EC980;
}

.login__text > a:link, .signup__text > a:link {
  color: white;
}

.login__text > a:visited, .signup__text > a:visited {
  color: #89C9AB;
}

.login__error, .signup__error {
  font-family: raleway;
  font-size: 20px;
  color: #C92E2E;
}


div {
  box-sizing: border-box;
}

.user-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.user, .user-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: Raleway;
}

.user-wrapper {
  transform: rotate(45deg) rotate3d(1, -1, 0, 45deg);
}

.loading {
  font-size: 25px;
  color: white;
}

.user__header-image {
  padding-right: 55px;
}

.user__header {
  margin-top: 60px;
  margin-bottom: 30px;
  font-size: 35px;
  color: white;
}

.user__logout-button {
  position: relative;
  top: 10px;
  align-self: flex-end;
  width: 10vw;
  max-width: 192px;
  min-width: 100px;
  height: 5vh;
  max-height: 96px;
  min-height: 50px;
  border: none;
  background-color: #C92E2E;
  color: white;
  cursor: pointer;
}

.user__logout-button:hover {
  background-color: #C95656;
}

.user__game-actions {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  max-width: 720px;
  width: 60vw;
  min-width: 400px;
  min-height: 100px;
  max-height: 140px; 
}

.game-actions__create-game, .game-actions__join-game {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  min-width: 200px;
  height: 10vh; 
  min-height: 50px;
  max-height: 70px; 
  border: none;
  box-shadow: inset 1px 1px 5px 0px rgba(29, 29, 29, 0.303);
  font-size: 25px;
  background-color: #2EC980;
  cursor: pointer;
}

.game-actions__create-game:hover, .game-actions__join-game:hover {
  background-color: #89C9AB;
}

.game-actions__create-game > i, .game-actions__join-game > i {
  margin: 10px; 
  font-size: 30px;
  pointer-events: none;
}

.join-game__form {
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  margin-right: 10px;
  grid-gap: 10px;
  gap: 10px;
  font-family: Raleway;
}

.join-game__input {
  border: none;
  box-shadow: inset 1px 1px 5px 0px rgba(29, 29, 29, 0.541);
  font-size: 20px;
  outline: none;
  width: 100px;
  flex-grow: 1;
  font-family: Raleway;
}

.join-game__submit {
  
  width: 5vw;
  max-width: 192px;
  min-width: 100px;
  height: 5vh;
  max-height: 96px;
  min-height: 50px;
  border: none;
  background-color: rgba(255, 255, 255, 0.253);
  font-size: 20px;
  color: black;
  border: 4px solid #222;
  cursor: pointer;
  font-family: Raleway;
}

.join-game__submit:hover {
  background-color: rgba(255, 255, 255, 0.687);
}

.join-game__text {
  margin-left: 10px;
}

.user__active-games {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  width: 60vw; 
  max-width: 720px;
  min-width: 400px;
  color: black;
}

.user__active-games-heading {
  margin-top: 20px;
  margin-bottom: 20px;
  color: white;
}

.user__active-games-divider {
  margin-bottom: 10px;
  color: white;
}

.active-games__game {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px;
  border: none;
  box-shadow: inset 1px 1px 5px 0px rgba(29, 29, 29, 0.303);
  background-color: #2EC980;
  cursor: pointer;
}

.active-games__game:hover {
  background-color: #89C9AB;
}

.active-games__game > i {
  margin-right: 10px;
  font-size: 30px;
}

.game__text {
  display: flex;
  flex-direction: column;
}

.error {
  font-size: 25px;
  color: #C92E2E;
}

svg {
  line-height: 0;
  margin: 0;
}

div {
  margin: 0;
  line-height: 0;
}

.cell {
  height: 6vmin;
  width: 6vmin;
  margin: -1px -1px 0 0;
}
.placement-cell {
  height: 8vmin;
  width: 8vmin;
  margin: -1px -1px 0 0;
}

.asset-container {
  height: 100%;
  width: 100%;
  position: relative;
  bottom: 9vmin;
  right: 3vmin;
  pointer-events: none;
}

.asset-container-placement {
  height: 100%;
  width: 100%;
  position: relative;
  bottom: 12vmin;
  right: 4vmin;
  pointer-events: none;
}


/** ANIMATION TEST **/

.asset-svg {
  animation: popUp 0.3s forwards;
  animation-timing-function: ease-in-out;
}

@keyframes popUp {
  0% { opacity: 0;}
  100% { opacity: 1;}
}
.opp-board-wrapper {
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
}

.board {
  display: flex;
}

.grid {
  display: flex;
}

.board__column {
  display: flex;
  flex-direction: column;
}

.turn-text {
  color: #2EC980;
}

.board-text {
  width: 48vmin;
  padding-right: 4vmin;
  margin-bottom: 2vmin;
}
.placement-inventory {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 200px;
}

.placed-inventory {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 200px;
  padding-left: 7vmin;
}

.placement-inventory__button--active {
  width: 16vmin;
  height: 8vmin;
  display: flex;
  cursor: pointer;
}

.placement-inventory__button {
  opacity: 0.5;
  width: 16vmin;
  height: 8vmin;
  display: flex;
  cursor: pointer;
}

.placement-interactions {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.inventory-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: 20px;
}

.orientation-buttons {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  margin-bottom: -8vmin;
  margin-right: -2vmin;
}

.asset-type-buttons {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  margin-bottom: -8vmin;
  margin-right: -2vmin;
}

.button-wrapper {
  position: relative;
  left: -3vmin;
  top: -9vmin;
  margin-right: -2vmin;
  pointer-events: none;
}

.ghost-button-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

}

.ghost-button {
  width: 6vmin;
  height: 6vmin;
}

.button-asset {
  width: 8vmin;
  height: 8vmin;
  position: relative;
  bottom: 4vmin;
  right: 4vmin;
}

.button-asset-size {
  font-size: 30px;
  color: #2EC980;
}
.placement-page {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
}
.placement-3d-text {
    margin-bottom: 40px;
    padding-right: 55px;
}

.board-wrapper {
    display: flex;
    transform: rotate(45deg) rotate3d(1, -1, 0, 45deg);
    grid-gap: 10vmin;
    gap: 10vmin;
}

.board-contents {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
}

.confirmation-area {
    height: 120px;
}

.board__confirm {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #2EC980;
    width: 45vw; 
    height: 10vh; 
    max-width: 400px;
    min-width: 300px;
    min-height: 50px;
    max-height: 70px;
    border: none;
    cursor: pointer;
    margin: 10px;
    font-family: Raleway;
    font-size: 30px;
}

.waiting-text {
    color: #2EC980;
}

/** Game over **/

.game-over {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border: 4px solid #2EC980;
    color: #2EC980;
    background-color: rgba(0, 0, 0, 0.4);
    width: 30vw;
    max-width: 500px;
    min-width: 400px;
    height: 20vh;
    max-height: 300px;
    min-height: 200px;
    z-index: 100;
    grid-gap: 10px;
    gap: 10px;
}

.game-over__message {
    font-size: 40px;
}

.game-over__exit {
    font-size: 30px;
    background-color: #2EC980;
    border: none;
    font-family: Raleway;
    cursor: pointer;
}

.game-over__exit:hover {
    background-color: #89C9AB;
}

.connecting-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
}

.connecting-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 30px;
    gap: 30px;
    transform: rotate(45deg) rotate3d(1, -1, 0, 45deg);
}

.connecting-info {
    display: flex;
    flex-direction: column;
    width: 60vw;
    max-width: 500px;
    grid-gap: 10px;
    gap: 10px;
}

.card-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #2EC980;
    padding: 10px;
}

.card-wrapper:hover {
    background-color: #89C9AB;
    cursor: pointer;
}
.card-wrapper:active {
    background-color: #b2dec9;
}

.card-wrapper > i {
    font-size: 30px;
}

.connecting-info__card {
    display: flex;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
}

.connecting-info__title {
    align-self: center;
    color: white;
}

.connecting-info__heading, .connecting-info__text {
    pointer-events: none;
}
