.placement-page {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
}
.placement-3d-text {
    margin-bottom: 40px;
    padding-right: 55px;
}

.board-wrapper {
    display: flex;
    transform: rotate(45deg) rotate3d(1, -1, 0, 45deg);
    gap: 10vmin;
}

.board-contents {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

.confirmation-area {
    height: 120px;
}

.board__confirm {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #2EC980;
    width: 45vw; 
    height: 10vh; 
    max-width: 400px;
    min-width: 300px;
    min-height: 50px;
    max-height: 70px;
    border: none;
    cursor: pointer;
    margin: 10px;
    font-family: Raleway;
    font-size: 30px;
}

.waiting-text {
    color: #2EC980;
}

/** Game over **/

.game-over {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border: 4px solid #2EC980;
    color: #2EC980;
    background-color: rgba(0, 0, 0, 0.4);
    width: 30vw;
    max-width: 500px;
    min-width: 400px;
    height: 20vh;
    max-height: 300px;
    min-height: 200px;
    z-index: 100;
    gap: 10px;
}

.game-over__message {
    font-size: 40px;
}

.game-over__exit {
    font-size: 30px;
    background-color: #2EC980;
    border: none;
    font-family: Raleway;
    cursor: pointer;
}

.game-over__exit:hover {
    background-color: #89C9AB;
}
